import React, { Component } from "react";
import { Table } from "reactstrap";

export default class ShipBreaks extends Component {
  render() {
    // limitBreaks child => {multiple array[]} (type: string)
    // devLevels child => {level, buffs(multiple array[])} (type: string)
    const limitBreaks     = this.props.limitBreaks;
    const devBreaks       = this.props.devBreaks;

    let limitBreaksFormat = null;
    if(limitBreaks) {
      const limitBreaks1 = devBreaks[0].join(" / ");
      const limitBreaks2 = devBreaks[1].join(" / ");
      const limitBreaks3 = devBreaks[2].join(" / ");

      limitBreaksFormat = (
        <>
          <thead>
            <tr>
              <th colSpan="3">Limit Breaks</th>
            </tr>
          </thead>
          <tbody>
            <tr>
              <th>1</th>
              <th>{limitBreaks1}</th>
            </tr>
            <tr>
              <th>2</th>
              <th>{limitBreaks2}</th>
            </tr>
            <tr>
              <th>3</th>
              <th>{limitBreaks3}</th>
            </tr>
          </tbody>
        </>
      );

    } else {
      const devLevels5  = devBreaks[0].buffs.join(" / ");
      const devLevels10 = devBreaks[1].buffs.join(" / ");
      const devLevels15 = devBreaks[2].buffs.join(" / ");
      const devLevels20 = devBreaks[3].buffs.join(" / ");
      const devLevels25 = devBreaks[4].buffs.join(" / ");
      const devLevels30 = devBreaks[5].buffs.join(" / ");

      limitBreaksFormat = (
        <>
          <thead>
            <tr>
              <th colSpan="3">Development Levels</th>
            </tr>
          </thead>
          <tbody>
            <tr>
              <th>5</th>
              <th>{devLevels5}</th>
            </tr>
            <tr>
              <th>10</th>
              <th>{devLevels10}</th>
            </tr>
            <tr>
              <th>15</th>
              <th>{devLevels15}</th>
            </tr>
            <tr>
              <th>20</th>
              <th>{devLevels20}</th>
            </tr>
            <tr>
              <th>25</th>
              <th>{devLevels25}</th>
            </tr>
            <tr>
              <th>30</th>
              <th>{devLevels30}</th>
            </tr>
          </tbody>
        </>
      );
    }

    return (
      <div>
        <Table className="tableCS">
          {limitBreaksFormat}
        </Table>
      </div>
    )
  }
}