import React, { Component } from "react";
import { Table } from "reactstrap";

import statAviation  from "assets/AL/Aviation.png";
import statFirepower from "assets/AL/Firepower.png";
import statReload    from "assets/AL/Reload.png";
import statTorpedo   from "assets/AL/Torpedo.png";
import resourceCoin  from "assets/AL/Coin.png";
import resourceMedal from "assets/AL/Medal.png";

export default class ShipMetadata extends Component {
  render() {
    const retrofit         = this.props.retrofit;
    const retrofitId       = this.props.retrofitId;
    const retrofitHullType = this.props.retrofitHullType;
    
    // Metadata
    const id               = this.props.id;
    const classType        = this.props.classType;
    const nationality      = this.props.nationality;
    const hullType         = this.props.hullType;
    const rarity           = this.props.rarity;
    const constructionTime = this.props.constructionTime;
    const obtainedFrom     = this.props.obtainedFrom;

    let idFormat           = id;
    let hullTypeFormat     = hullType;
    let rarityFormat       = rarity;
    if(retrofit) {
      idFormat = id + " → " + retrofitId;
      if(retrofitHullType !== hullType) {
        hullTypeFormat = hullType + " → " + retrofitHullType;
      }
      if(rarity === "Normal"    ) {rarityFormat = rarity + " →  Rare"}
      if(rarity === "Rare"      ) {rarityFormat = rarity + " →  Elite"}
      if(rarity === "Elite"     ) {rarityFormat = rarity + " →  Super Rare"}
      if(rarity === "Super Rare") {rarityFormat = rarity + " →  Ultra Rare"}
    }

    // Metadata with Child
    // exist child        => {en, cn, jp, kr}                            (type: string)
    // availableIn child  => {light, heavy, aviation, limited, exchange} (type: bool, string)
    // enhanceValue child => {firepower, torpedo, aviation, reload}      (type: number) 
    // scrapValue child   => {coin, oil, medal}                          (type: number)
    const exist        = this.props.exist;
    const availableIn  = this.props.availableIn;
    const enhanceValue = this.props.enhanceValue;
    const scrapValue   = this.props.scrapValue;

    // exist
    const existMarks = (existBool) => {if(existBool) {return `✔`} else {return `✘`}}
    const existEN = exist.en;
    const existCN = exist.cn;
    const existJP = exist.jp;
    const existKR = exist.kr;
    const existFormat = `EN:${existMarks(existEN)} | CN:${existMarks(existCN)} | JP:${existMarks(existJP)} | KR:${existMarks(existKR)}`;

    // availableIn
    const availMarks  = (type, availBool) => {if(availBool) {return type+":✔"} else {return false}};
    const aInLight    = availMarks("Light",availableIn.light);
    const aInHeavy    = availMarks("Heavy",availableIn.heavy);
    const aInAviation = availMarks("Aviation",availableIn.aviation);
    const aInLimited  = availMarks("Limited",availableIn.limited);
    const aInExchange = availMarks("Medal",availableIn.exchange);
    const availableInArray = [aInLight, aInHeavy, aInAviation, aInLimited, aInExchange].filter(Boolean).join(" | ");;
    let availableInFormat  = null;
    if(availableInArray) {availableInFormat = availableInArray} else {availableInFormat = `-`}

    // enhanceValue
    let enhanceValueFormat = null;
    if(enhanceValue) {
      const firepower    = (<><img src={statFirepower} referrerPolicy="no-referrer" alt="..." className="img-fluid" /> {enhanceValue.firepower}</>);
      const torpedo      = (<><img src={statTorpedo  } referrerPolicy="no-referrer" alt="..." className="img-fluid" /> {enhanceValue.torpedo  }</>);
      const aviation     = (<><img src={statAviation } referrerPolicy="no-referrer" alt="..." className="img-fluid" /> {enhanceValue.aviation }</>);
      const reload       = (<><img src={statReload   } referrerPolicy="no-referrer" alt="..." className="img-fluid" /> {enhanceValue.reload   }</>);
      enhanceValueFormat = (<>{firepower} | {torpedo} | {aviation} |  {reload}</>);
    }

    // scrapValue
    let scrapValueFormat = null;
    if(scrapValue) {
      // no oil after q4 2021 patch
      const coin  = scrapValue.coin;
      const medal = scrapValue.medal;
      let coinFormat  = null;
      let medalFormat = null;
      if(coin) {coinFormat =(<><img src={resourceCoin} referrerPolicy="no-referrer" alt="..." className="img-fluid" /> {coin}</>)}
      if(medal) {medalFormat =(<> | <img src={resourceMedal} referrerPolicy="no-referrer" alt="..." className="img-fluid" /> {medal}</>)}
      scrapValueFormat = (<>{coinFormat}{medalFormat}</>);
    }

    // obtainedFormat
    let obtainedFromFormat = obtainedFrom;
    if(obtainedFrom) {
     obtainedFromFormat = obtainedFrom.split('\n').map((line, i) => (<span key={i}>{line}<br/></span>));
    }
    return (
      <div>
        <div className="dis-inlinetable shipMetadata">
          <Table>
            <thead>
              <tr>
                <th>ID</th>
                <th>{idFormat}</th>
              </tr>
              <tr>
                <th>Class</th>
                <th>{classType}</th>
              </tr>
              <tr>
                <th>Faction</th>
                <th>{nationality}</th>
              </tr>
              <tr>
                <th>Classification</th>
                <th>{hullTypeFormat}</th>
              </tr>
            </thead>
          </Table>
        </div>
        <div className="dis-inlinetable shipMetadata">
          <Table>
            <thead>
              <tr>
                <th>Rarity</th>
                <th>{rarityFormat}</th>
              </tr>
              <tr>
                <th>Construction</th>
                <th>{constructionTime}</th>
              </tr>
              <tr>
                <th>Exist</th>
                <th>{existFormat}</th>
              </tr>
              
              <tr>
                <th>Available</th>
                <th>{availableInFormat}</th>
              </tr>
            </thead>
          </Table>
        </div>
        <div className="dis-inlinetable shipMetadata">
          <Table>
            <thead>
              <tr>
                <th>Enhance</th>
                <th>{enhanceValueFormat}</th>
              </tr>
            </thead>
          </Table>
        </div>
        <div className="dis-inlinetable shipMetadata">
          <Table>
            <thead>
              <tr>
                <th>Scrap</th>
                <th>{scrapValueFormat}</th>
              </tr>
            </thead>
          </Table>
        </div>
        <div>
          <Table>
            <thead>
              <tr>
                <th>{obtainedFromFormat}</th>
              </tr>
            </thead>
          </Table>
        </div>
      </div>
    )
  }
}