import React, { Component } from "react";
import { Redirect } from "react-router-dom";
import ShipProfile from "views/components/Ship/ShipProfile";

export default class ShipFetch extends Component {
  constructor(props) {
    super(props);
    this.state = {
      error: null,
      isLoaded: false,
      azurAPI: [],
      shipBanner: []
    };
  }

  componentDidMount() {
    this.mounted = true;
    Promise.all([
      fetch("https://raw.githubusercontent.com/AzurAPI/azurapi-js-setup/master/ships.json")
      .then(res => res.json()),
      fetch("https://raw.githubusercontent.com/Fernando2603/AzurLane/main/ShipBanner.json")
      .then(res => res.json())
    ]).then(
      ([azurAPI, shipBanner]) => {
        if(this.mounted) {
          this.setState({
            isLoaded: true,
            azurAPI: azurAPI,
            shipBanner: shipBanner
          })
        }
      },
      (error) => {
        this.setState({
          isLoaded: true,
          error
        })
      }
    )
  }

  componentWillUnmount() {
    this.mounted = false;
  }

  render() {
    const { error, isLoaded, azurAPI, shipBanner } = this.state;
    if (error) {
      return <div>Error: {error.message}</div>;
    } else if (!isLoaded) {
      return <div>Loading...</div>;
    } else {
      const shipName   = window.location.href.split("/").slice(-1)[0];
      const shipData   = azurAPI.find(ship => ship.names.en.toLowerCase().replace(/\s+/g, "_") === shipName.toLowerCase());
      const bannerData = shipBanner.find(banner => banner.names === shipName);
      if(shipData === undefined) {
        return <Redirect to="/ship" />
      } else {
        return <ShipProfile shipData={shipData} bannerData={bannerData} /> 
      }
    }
  }
}