import React, { useState, useEffect } from "react";
import { Switch, Route } from "react-router-dom";

// Ship Components
import ShipRoute from "views/components/Ship/ShipRoute";
import ShipList from "views/components/Ship/ShipList";

function Ship() {
  const [error, setError] = useState(null);
  const [isLoaded, setIsLoaded] = useState(false);
  const [azurAPI, setAzurAPI] = useState([]);

  const [bannerIsLoaded, setBannerIsLoaded] = useState(false);
  const [shipBanner, setShipBanner] = useState([]);

  useEffect(() => {
    fetch("https://raw.githubusercontent.com/AzurAPI/azurapi-js-setup/master/ships.json")
    .then(res => res.json())
    .then(
      (result) => {
        setIsLoaded(true);
        setAzurAPI(result);
      },
      (error) => {
        setIsLoaded(true);
        setError(error);
      }
    );
    fetch("https://raw.githubusercontent.com/Fernando2603/AzurLane/main/ShipBanner.json")
    .then(res => res.json())
    .then(
      (result) => {
        setBannerIsLoaded(true);
        setShipBanner(result);
      },
      (error) => {
        setBannerIsLoaded(true);
        setError(error);
      }
    )
  }, [])

  if (error) {
    return <div>Error: {error.message}</div>;
  } else if (!isLoaded) {
    return <div>Loading AzurAPI...</div>;
  } else if (!bannerIsLoaded) {
    return <div>Loading ShipBanner...</div>; 
  } else {
    return (
      <>
        <Switch>
          <Route path="/ship/:id" render={({ match }) => {
            const id         = match.params.id.toLowerCase();
            const shipData   = azurAPI.find(ship => ship.names.en.toLowerCase().replace(/\s+/g, "_") === id);
            const bannerData = shipBanner.find(banner => banner.names.toLowerCase().replace(/\s+/g, "_") === id);
            return <ShipRoute shipData={shipData} bannerData={bannerData} />
          }} />
          <Route path="/ship" render={() => {
            return <ShipList azurAPI={azurAPI} shipBanner={shipBanner}/>
          }} />
        </Switch>
      </>
    );
  }
}

export default Ship;
