import React, { Component } from "react";
import { Table } from "reactstrap";

export default class ShipSkills extends Component {
  render() {
    // Skills child              => {multiple array[]} (type: string)
    // Skills[Array] child       => {icon, names, description, color} (type: string)
    // Skills[Array].names child => {en, cn, jp} (type: string)
    const skills = this.props.skills;

    return (
      <div>
        <Table className="tableCS">
          <thead>
            <tr>
              <th colSpan="2">Skill</th>
            </tr>
          </thead>
          <tbody>
            {skills.map((skill, key) => {
              const name = skill.names.en;
              const icon = skill.icon;
              const desc = skill.description;
              const type = skill.color;
              return (
                <tr key={key} className={"skill-"+type}>
                  <th>
                    <img src={icon} referrerPolicy="no-referrer" alt="..." className="img-fluid" />
                    <br />
                    {name}
                  </th>
                  <th>
                    {desc.split('\n').map((line, i) => (
                      <span key={i}>
                        {line}
                        <br/>
                      </span>
                    ))}
                  </th>
                </tr>
              );
            })}
          </tbody>
        </Table>
      </div>
    )
  }
}